.navbar {
  background: #1976d2;
  border-bottom: none;
  box-shadow: 0 8px 32px rgba(25, 118, 210, 0.18);
  min-height: 72px;
  padding: 0 2.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-title {
  font-size: 2.2rem;
  font-weight: 900;
  color: #1976d2;
  background: none;
  letter-spacing: 1px;
}

.nav-links {
  display: flex;
  gap: 1.2rem;
  align-items: center;
}

.nav-btn {
  color: #fff;
  background: #1976d2;
  font-size: 1.1rem;
  font-weight: 700;
  border-radius: 18px;
  padding: 0.7rem 1.5rem;
  border: none;
  box-shadow: 0 2px 12px #1565c055;
  position: relative;
  overflow: hidden;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s, filter 0.2s;
  z-index: 1;
  outline: none;
  text-decoration: none;
  display: inline-block;
  margin: 0 2px;
}

.nav-btn.active {
  background: #1565c0;
  color: #fff;
  box-shadow: 0 4px 18px #1976d288;
}

.nav-btn:active, .nav-btn.clicked {
  animation: blinkBtn 0.25s linear;
}

@keyframes blinkBtn {
  0% { box-shadow: 0 0 0 0 #fff, 0 4px 24px #1976d255; }
  50% { box-shadow: 0 0 16px 8px #fff, 0 8px 32px #1976d288; background: #fff; color: #1976d2; }
  100% { box-shadow: 0 4px 24px #1976d255; }
}

// Mobile menu styles
.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);

  span {
    width: 25px;
    height: 3px;
    background: white;
    margin: 3px 0;
    transition: all 0.3s ease;
    border-radius: 2px;
  }

  &.active {
    span:nth-child(1) {
      transform: rotate(45deg) translate(5px, 5px);
    }
    span:nth-child(2) {
      opacity: 0;
    }
    span:nth-child(3) {
      transform: rotate(-45deg) translate(7px, -6px);
    }
  }
}

// Responsive design
@media (max-width: 768px) {
  .navbar {
    padding: 1rem;
  }

  .navbar-title {
    font-size: 1.5rem;
  }

  .nav-links {
    position: fixed;
    top: 100%;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    flex-direction: column;
    padding: 2rem;
    gap: 1rem;
    transform: translateY(-100%);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    border-top: 1px solid rgba(135, 206, 235, 0.2);

    &.active {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
    }

    a {
      width: 100%;
      text-align: center;
      color: #1e90ff;
      background: rgba(135, 206, 235, 0.1);
      border: 1px solid rgba(135, 206, 235, 0.2);

      &:hover {
        background: rgba(135, 206, 235, 0.2);
        color: #87ceeb;
      }

      &.active {
        background: linear-gradient(45deg, #87ceeb, #00bfff);
        color: white;
      }
    }
  }

  .mobile-menu-toggle {
    display: flex;
  }
}

@media (max-width: 480px) {
  .navbar {
    padding: 0.75rem;
  }

  .navbar-title {
    font-size: 1.3rem;
  }
}

// Scroll effect
.navbar.scrolled {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  box-shadow: 0 4px 20px rgba(135, 206, 235, 0.1);

  .navbar-title {
    background: linear-gradient(45deg, #1e90ff, #87ceeb);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .nav-links a {
    color: #1e90ff;
    background: rgba(135, 206, 235, 0.1);

    &:hover {
      background: rgba(135, 206, 235, 0.2);
      color: #87ceeb;
    }

    &.active {
      background: linear-gradient(45deg, #87ceeb, #00bfff);
      color: white;
    }
  }
}
