// Home.scss - Modern and attractive styling for the home page with sky blue theme

.home-container {
  background: linear-gradient(135deg, #e3f0ff 0%, #b3d8fd 100%);
  min-height: 100vh;
  padding-bottom: 40px;
  overflow-x: hidden;
}

// Hero Section
.hero-section {
  min-height: 100vh;
  background: linear-gradient(135deg, #e3f0ff 0%, #b3d8fd 100%);
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 5%;
  animation: fadeInUp 0.8s cubic-bezier(.23,1.01,.32,1) 0.1s both;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="1" fill="white" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
    opacity: 0.3;
  }
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 2rem;
  }
}

.hero-text {
  animation: fadeInLeft 1s ease-out;
}

.hero-title {
  margin-bottom: 2.2rem;
  
  .greeting {
    display: block;
    font-size: 1.5rem;
    color: rgba(255, 255, 255, 0.95);
    font-weight: 400;
    margin-bottom: 0.5rem;
  }
  
  .name {
    display: block;
    font-size: 4.5rem;
    font-weight: 900;
    color: white;
    text-shadow: 0 6px 24px #1976d288, 0 1px 0 #fff;
    background: linear-gradient(90deg, #1976d2 30%, #64b5f6 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    letter-spacing: 2px;
    margin-bottom: 0.5rem;
    padding: 0 0.5rem;
    
    @media (max-width: 768px) {
      font-size: 3rem;
    }
  }
}

.hero-subtitle {
  font-size: 1.5rem;
  color: #e6f3ff;
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.hero-description {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.95);
  line-height: 1.6;
  margin-bottom: 2.5rem;
  max-width: 500px;
  
  strong {
    color: #e6f3ff;
  }
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  
  @media (max-width: 768px) {
    justify-content: center;
  }
}

.btn {
  padding: 1.1rem 2.5rem;
  border: none;
  border-radius: 16px;
  font-size: 1.2rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s, transform 0.2s;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 4px 24px #1976d255;
  margin-right: 1rem;
  margin-bottom: 0.5rem;
  background: linear-gradient(90deg, #1976d2 60%, #64b5f6 100%);
  color: #fff;
  
  &:hover {
    background: linear-gradient(90deg, #1565c0 60%, #42a5f5 100%);
    box-shadow: 0 8px 32px #1976d288;
    transform: translateY(-2px) scale(1.05);
  }
  
  &.btn-secondary {
    background: #fff;
    color: #1976d2;
    border: 2px solid #1976d2;
    
    &:hover {
      background: #e3f0ff;
      color: #1565c0;
      border-color: #1565c0;
    }
  }
}

.btn:active, .btn.clicked {
  animation: blinkBtn 0.25s linear;
}
@keyframes blinkBtn {
  0% { box-shadow: 0 0 0 0 #fff, 0 4px 24px #1976d255; }
  50% { box-shadow: 0 0 16px 8px #fff, 0 8px 32px #1976d288; background: #fff; color: #1976d2; }
  100% { box-shadow: 0 4px 24px #1976d255; }
}

// Hero Image Section
.hero-image {
  display: flex;
  justify-content: center;
  animation: fadeInRight 1s ease-out;
}

.profile-photo {
  position: relative;
  
  .profile-img {
    width: 350px;
    height: 450px;
    object-fit: cover;
    border-radius: 20px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease;
    
    &:hover {
      transform: scale(1.05);
    }
    
    @media (max-width: 768px) {
      width: 280px;
      height: 360px;
    }
  }
  
  .photo-frame {
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    border: 3px solid rgba(135, 206, 235, 0.8);
    border-radius: 25px;
    z-index: -1;
  }
}

.profile-photo.interactive {
  cursor: pointer;
  position: relative;
  transition: box-shadow 0.3s, transform 0.3s;
  box-shadow: 0 8px 32px rgba(30, 144, 255, 0.15);
  outline: none;
}
.profile-photo.interactive:hover,
.profile-photo.interactive:focus {
  box-shadow: 0 12px 40px 0 #b3d8fd, 0 0 0 4px #e3f0ff;
  transform: scale(1.04) rotate(-2deg);
}
.profile-photo .photo-hover-text {
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(30, 144, 255, 0.85);
  color: #fff;
  padding: 4px 16px;
  border-radius: 12px;
  font-size: 1rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s;
}
.profile-photo.interactive:hover .photo-hover-text,
.profile-photo.interactive:focus .photo-hover-text {
  opacity: 1;
}

/* Modal styles */
.photo-modal {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(179, 216, 253, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: fadeIn 0.3s;
}
.modal-content {
  background: #fff;
  border-radius: 24px;
  box-shadow: 0 16px 48px rgba(25, 118, 210, 0.12);
  padding: 32px 32px 24px 32px;
  position: relative;
  text-align: center;
  max-width: 95vw;
  max-height: 90vh;
  animation: popIn 0.3s;
}
.modal-photo-img {
  width: 320px;
  max-width: 80vw;
  height: auto;
  border-radius: 18px;
  box-shadow: 0 8px 32px rgba(30, 144, 255, 0.18);
  margin-bottom: 18px;
}
.modal-photo-caption {
  font-size: 1.2rem;
  color: #1e90ff;
  margin-bottom: 12px;
}
.close-modal-btn {
  position: absolute;
  top: 12px;
  right: 18px;
  background: #e3f0ff;
  color: #1976d2;
  border: none;
  font-size: 2rem;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(30, 144, 255, 0.08);
  transition: background 0.2s;
}
.close-modal-btn:hover {
  background: #b3d8fd;
  color: #fff;
}

// Floating Elements
.floating-elements {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1;
}

.floating-circle {
  position: absolute;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  animation: float 6s ease-in-out infinite;
  filter: blur(0.5px);
  opacity: 0.85;
  
  &.circle-1 {
    width: 80px;
    height: 80px;
    top: 20%;
    right: 10%;
    animation-delay: 0s;
  }
  
  &.circle-2 {
    width: 60px;
    height: 60px;
    top: 60%;
    left: 5%;
    animation-delay: 2s;
  }
  
  &.circle-3 {
    width: 40px;
    height: 40px;
    top: 80%;
    right: 20%;
    animation-delay: 4s;
  }
}

.floating-square {
  position: absolute;
  background: rgba(135, 206, 235, 0.3);
  animation: float 8s ease-in-out infinite;
  filter: blur(0.5px);
  opacity: 0.85;
  
  &.square-1 {
    width: 50px;
    height: 50px;
    top: 30%;
    left: 10%;
    transform: rotate(45deg);
    animation-delay: 1s;
  }
  
  &.square-2 {
    width: 30px;
    height: 30px;
    top: 70%;
    right: 5%;
    transform: rotate(45deg);
    animation-delay: 3s;
  }
}

// Stats Section
.stats-section {
  background: linear-gradient(135deg, #f7fbff 0%, #e3f0ff 100%);
  padding: 4rem 0;
}

.stats-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 5%;
}

.stat-item {
  text-align: center;
  padding: 2rem;
  background: white;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(135, 206, 235, 0.1);
  transition: transform 0.3s ease;
  border: 1px solid rgba(135, 206, 235, 0.1);
  
  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 25px rgba(135, 206, 235, 0.2);
  }
  
  .stat-number {
    font-size: 3rem;
    font-weight: 800;
    color: #1976d2;
    margin-bottom: 0.5rem;
  }
  
  .stat-label {
    font-size: 1.1rem;
    color: #1976d2;
    font-weight: 500;
  }
}

// Skills Preview Section
.skills-preview {
  background: linear-gradient(135deg, #f7fbff 0%, #e3f0ff 100%);
  padding: 5rem 0;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 700;
  color: #1976d2;
  margin-bottom: 3rem;
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 4px;
    background: linear-gradient(45deg, #87ceeb, #00bfff);
    border-radius: 2px;
  }
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 5%;
}

.skill-card {
  background: white;
  padding: 2.5rem 2rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(25, 118, 210, 0.08);
  transition: all 0.3s ease;
  border: 1px solid #b3d8fd;
  
  &:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 40px rgba(135, 206, 235, 0.2);
    border-color: rgba(135, 206, 235, 0.3);
  }
  
  .skill-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: #1e90ff;
    margin-bottom: 1rem;
  }
  
  p {
    color: #87ceeb;
    line-height: 1.6;
  }
}

// Animations
@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(40px); }
  to { opacity: 1; transform: translateY(0); }
}

// Responsive Design
@media (max-width: 768px) {
  .hero-section {
    padding: 2rem 5%;
  }
  
  .hero-content {
    gap: 2rem;
  }
  
  .hero-title .name {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .stats-container {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .stats-container {
    grid-template-columns: 1fr;
  }
  
  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .btn {
    width: 100%;
    max-width: 250px;
  }
} 