/* Global CSS - Modern and attractive styling with sky blue theme */

* {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #e3f0ff 0%, #b3d8fd 100%);
  min-height: 100vh;
  background-size: cover;
  background-attachment: fixed;
  scroll-behavior: smooth;
  line-height: 1.6;
  color: #333;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #e6f3ff;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(45deg, #b3d8fd, #e3f0ff);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(45deg, #00bfff, #1e90ff);
}

/* Main container */
.main-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Page container for other pages */
.page-container {
  max-width: 1200px;
  margin: 100px auto 40px;
  padding: 40px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(25, 118, 210, 0.08);
  backdrop-filter: blur(10px);
  border: 1px solid #b3d8fd;
}

/* Typography improvements */
h1, h2, h3, h4, h5, h6 {
  margin: 0 0 1rem 0;
  font-weight: 600;
  line-height: 1.2;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

h2 {
  font-size: 2rem;
  font-weight: 600;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
}

p {
  margin: 0 0 1rem 0;
  line-height: 1.7;
}

/* Button styles */
.btn {
  display: inline-block;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.btn-primary {
  background: linear-gradient(45deg, #b3d8fd, #1976d2);
  color: white;
  box-shadow: 0 4px 15px rgba(135, 206, 235, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(135, 206, 235, 0.4);
  background: linear-gradient(45deg, #1976d2, #b3d8fd);
}

.btn-secondary {
  background: transparent;
  color: #87ceeb;
  border: 2px solid #87ceeb;
}

.btn-secondary:hover {
  background: #87ceeb;
  color: white;
  transform: translateY(-2px);
}

/* Card styles */
.card {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(25, 118, 210, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid #b3d8fd;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(135, 206, 235, 0.15);
}

/* Section styles */
.section {
  padding: 4rem 0;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 700;
  color: #1976d2;
  margin-bottom: 3rem;
  position: relative;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(45deg, #87ceeb, #00bfff);
  border-radius: 2px;
}

/* Grid layouts */
.grid {
  display: grid;
  gap: 2rem;
}

.grid-2 {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.grid-3 {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.grid-4 {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

/* Responsive design */
@media (max-width: 768px) {
  .page-container {
    margin: 80px 20px 20px;
    padding: 30px 20px;
  }
  
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.75rem;
  }
  
  h3 {
    font-size: 1.25rem;
  }
  
  .section {
    padding: 3rem 0;
  }
  
  .section-title {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .page-container {
    margin: 70px 15px 15px;
    padding: 20px 15px;
  }
  
  .grid-2,
  .grid-3,
  .grid-4 {
    grid-template-columns: 1fr;
  }
}

/* Animation classes */
.fade-in {
  animation: fadeIn 0.6s ease-in;
}

.slide-up {
  animation: slideUp 0.6s ease-out;
}

.slide-down {
  animation: slideDown 0.6s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Loading animation */
.loading {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 3px solid rgba(135, 206, 235, 0.3);
  border-radius: 50%;
  border-top-color: #87ceeb;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.page-fade {
  animation: fadeInPage 0.6s cubic-bezier(.23,1.01,.32,1);
}
@keyframes fadeInPage {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}
