// Modern page background
.modern-page-bg {
  min-height: 100vh;
  background: linear-gradient(135deg, #e3f0ff 0%, #b3d8fd 100%);
  position: relative;
  padding: 0 0 60px 0;
}

// Floating elements for all pages
.floating-elements {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  pointer-events: none;
  z-index: 0;
}
.floating-circle {
  position: absolute;
  border-radius: 50%;
  background: rgba(25, 118, 210, 0.08);
  filter: blur(1.5px);
  opacity: 0.7;
}
.floating-circle.circle-1 {
  width: 120px; height: 120px;
  top: 10%; left: 8%;
  animation: float 7s ease-in-out infinite;
}
.floating-circle.circle-2 {
  width: 80px; height: 80px;
  bottom: 12%; right: 10%;
  animation: float 9s ease-in-out infinite;
}
.floating-square {
  position: absolute;
  background: rgba(25, 118, 210, 0.10);
  border-radius: 18px;
  width: 60px; height: 60px;
  top: 60%; left: 5%;
  animation: float 11s ease-in-out infinite;
}
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-18px); }
}

// Modern card
.modern-card {
  max-width: 650px;
  margin: 80px auto 0 auto;
  background: #fff;
  border-radius: 22px;
  box-shadow: 0 10px 40px rgba(25, 118, 210, 0.13);
  padding: 40px 36px 32px 36px;
  position: relative;
  z-index: 1;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #222;
  text-align: left;
  transition: box-shadow 0.2s;
}
@media (max-width: 600px) {
  .modern-card { padding: 24px 8px 18px 8px; }
}

// Modern heading
.modern-heading {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 900;
  background: linear-gradient(90deg, #1976d2 30%, #64b5f6 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 32px;
  letter-spacing: 1.5px;
  text-shadow: 0 6px 24px #1976d288, 0 1px 0 #fff;
}

// Modern section
.modern-section {
  margin-bottom: 28px;
}
.modern-subheading {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1976d2;
  margin-bottom: 10px;
}

// Modern contact list
.modern-contact-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 1.08rem;
  color: #1976d2;
}
.modern-contact-list a {
  color: #1976d2;
  text-decoration: underline;
}

// Modern skill list
.modern-skill-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 18px;
}
.modern-skill-card {
  background: #f7fbff;
  border-radius: 12px;
  box-shadow: 0 2px 10px rgba(25, 118, 210, 0.08);
  padding: 18px 0;
  text-align: center;
  font-size: 1.12rem;
  font-weight: 600;
  color: #1976d2;
  transition: background 0.2s, box-shadow 0.2s, transform 0.2s;
}
.modern-skill-card:hover {
  background: #e3f0ff;
  box-shadow: 0 6px 18px #1976d288;
  transform: translateY(-3px) scale(1.04);
}

// Modern list for achievements, academics, etc.
.modern-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.modern-list-card {
  background: #f7fbff;
  border-radius: 14px;
  box-shadow: 0 2px 10px rgba(25, 118, 210, 0.08);
  padding: 20px 22px;
  font-size: 1.08rem;
  color: #1976d2;
  transition: background 0.2s, box-shadow 0.2s, transform 0.2s;
}
.modern-list-card:hover {
  background: #e3f0ff;
  box-shadow: 0 8px 24px #1976d288;
  transform: translateY(-3px) scale(1.03);
}

.solid-blue {
  background: none !important;
  color: #1976d2 !important;
  -webkit-background-clip: unset !important;
  -webkit-text-fill-color: unset !important;
  background-clip: unset !important;
  text-shadow: none !important;
} 